import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { IonContent, ModalController, NavController } from '@ionic/angular';
import { TranslateService } from "@ngx-translate/core";
import { TranslateConfigService } from "../../../src/services/translate-config/translate-config.service";
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { SecureStoragePlugin } from "capacitor-secure-storage-plugin";
import { Keyboard } from "@capacitor/keyboard";
import { environment } from 'src/environments/environment';
import { ClaimFormService } from 'src/services/claim-form/claim-form.service';
import { CustomSelectComponent } from 'src/components/custom-select/custom-select.component';
import { UrlService } from 'src/services/url/url.service';

@Component({
  selector: 'app-claim-form',
  templateUrl: './claim-form.page.html',
  styleUrls: ['./claim-form.page.scss'],
})
export class ClaimFormPage implements OnInit {
  @ViewChild("claimForm") claimForm: ElementRef;
	@ViewChild("content", { read: ElementRef, static: false }) content: ElementRef;
	@ViewChild("content") ioncontent: IonContent;
  formData: any = this.claimFormService.getCurrentValue();

  readonly assetUrl = environment.assetURL;

	claimFormGroup: FormGroup;

  previousUrl: string[];
	prevUrl: string;

  persons: Array<any> = [{name: "Sok Arina", id: "1"},{name: "Pak Rina", id: "2"}];
	selectedPerson: any = null;

	banks: Array<any> = [{bankName: "Maybank", bankCode: "MB"}, {bankName: "CIMB", bankCode: "CIMB"}, {bankName: "Public Bank", bankCode: "PB"}, {bankName: "Hong Leong Bank", bankCode: "HLB"}, {bankName: "RHB Bank", bankCode: "RHB"}, {bankName: "UOB", bankCode: "UOB"}, {bankName: "OCBC", bankCode: "OCBC"}, {bankName: "HSBC", bankCode: "HSBC"}, {bankName: "Standard Chartered", bankCode: "SCB"}, {bankName: "Affin Bank", bankCode: "AFFIN"}, {bankName: "Bank Islam", bankCode: "BANKISLAM"}, {bankName: "Bank Rakyat", bankCode: "BANKRAKYAT"}, {bankName: "Agro Bank", bankCode: "AGROBANK"}];
	selectedBank: any = null;

  loaded: boolean;

  async openSelectBankModal() {
    const selectOptions = this.banks.map(bank => ({
      label: bank.bankName,
      value: bank.bankCode,
      selected: this.selectedBank != null ? this.selectedBank?.bankCode === bank.bankCode ? true : false : false
    }));
    const modal = await this.modalCtrl.create({
      cssClass: 'custom-select-modal',
      component: CustomSelectComponent,
      initialBreakpoint: 0.65,
      componentProps: { 
        items: selectOptions,
        header: this.translate.instant("ADDBANKINFO_PAGE.SELECT_BANK")
      }
    });

    await modal.present();

    const { data } = await modal.onWillDismiss();
    if (data) {
      this.selectedBank = this.banks.find(i => i.bankCode === data.value);
    }
  }

  async openSelectPersonModal() {
    const selectOptions = this.persons.map(person => ({
      label: person.name,
      value: person.id,
      selected: this.selectedPerson != null ? this.selectedPerson?.id === person.id ? true : false : false
    }));
    const modal = await this.modalCtrl.create({
      cssClass: 'custom-select-modal',
      component: CustomSelectComponent,
      initialBreakpoint: 0.65,
      componentProps: { 
        items: selectOptions,
        header: this.translate.instant("ADDBANKINFO_PAGE.SELECT_BANK")
      }
    });

    await modal.present();

    const { data } = await modal.onWillDismiss();
    if (data) {
      this.selectedPerson = this.persons.find(i => i.id === data.value);
    }
  }

  goBack() {
    this.navCtrl.navigateBack("/claim-tnc", {
			replaceUrl: true,
			animated: true,
		});
  }

  submitForm(){}

  reset() {
	}

  constructor(
    public formBuilder: FormBuilder,
    private navCtrl: NavController, 
    public translate: TranslateService,
		public translateConfigService: TranslateConfigService,
    private claimFormService: ClaimFormService,
    private modalCtrl: ModalController,
    public urlService: UrlService
  ) { 
    this.translateConfigService.getDefaultLanguage();
  }

  async ngOnInit() {
    this.urlService.previousUrl$.subscribe((previousUrl: string[]) => {
			this.previousUrl = previousUrl;
		});
		this.prevUrl = this.previousUrl[this.previousUrl.length - 1];

    this.claimFormGroup = new FormGroup({
			insuredperson: new FormControl(),
			contactno: new FormControl(),
			bankname: new FormControl(),
			bankaccno: new FormControl(),
			holdername: new FormControl(),
		});

    this.claimFormGroup = this.formBuilder.group({
			insuredperson: ["", [Validators.required]],
			contactno: ["", [Validators.required]],
			bankname: ["", [Validators.required]],
			bankaccno: ["", [Validators.required]],
			holdername: ["", [Validators.required]],
		});

    try {
      this.claimFormGroup.controls["bankaccno"].setValue((await SecureStoragePlugin.get({ key:"bankaccno"})).value || "");
      this.claimFormGroup.controls["holdername"].setValue((await SecureStoragePlugin.get({ key:"holdername"})).value || "");
    } catch (e) {
      console.log("No bank info found");
    }
  }

  ionViewWillLeave() {
		Keyboard.removeAllListeners();
	}

}
